import React, { useState } from 'react'
import { getTopList, getTopListByBrand } from './backend/backend'

const Table = () => {
  const topList = getTopList()
  const topListByBrands = getTopListByBrand()

  const [showBrands, setShowBrands] = useState(false)

  const [showInstructions, setShowInstructions] = useState(false)

  return (
    <div className={'statistics'}>
      <h1>Tilastot</h1>
      <div style={{ padding: '1rem' }}>
        <span onClick={() => setShowInstructions(!showInstructions)}>
          {showInstructions ? 'Piilota ohjeet' : 'Nyt ohjeet'}
        </span>
      </div>
      {showInstructions && (
        <div className="instructions">
          <p>Tilastossa voit vertailla kaikkia aineiston merkkej ja malleja.</p>
          <p>
            Taulukossa kukin merkki ja malli on pisteytetty sen mukaan, kuinka paljon keskimrist autoa parempi se
            keskimrin on. Esimerkiksi, jos keskimrisen 100tkm ajetun auton hylkysprosentti on 9.8%, ja
            vertailtavan auton vastaava hylkysprosentti on 5.2% saa se 4.6 pistett.
          </p>
          <p>
            Kaikista pisteist lasketaan keskiarvo, jolloin esimerkiksi -10 kokonaispistett tarkoittaa, ett kyseisen
            auton hylkysprosentti on keskimrin 10 prosenttiyksikk huonompi, kuin keskimrisell autolla.
          </p>
        </div>
      )}
      <button className={'table-button'} onClick={() => setShowBrands(!showBrands)}>
        Nyt {showBrands ? 'mallit' : 'merkit'}
      </button>
      <div className="table-container">
        <table cellSpacing={0}>
          <thead>
            <tr>
              <th></th>
              <th>{showBrands ? 'Merkki' : 'Malli'}</th>
              <th className="align-right">Pisteet</th>
            </tr>
          </thead>
          <tbody>
            {(showBrands ? topListByBrands : topList).map((l, i) => (
              <tr key={l[0]}>
                <td>{i + 1}.</td>
                <td>{l[0]}</td>
                <td className="align-right">{l[1].toFixed(2)}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  )
}

export default Table
